* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  font-family: Arial, Helvetica, sans-serif;
  background: #ccc;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

.App {
  &__title {
    padding-top: 30px;
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
  }
}
